body {
  color: #a8cff2;
  background: #121212;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, sans-serif;
  overflow-x: hidden;
}

.app-title {
  color: #2989d7;
  letter-spacing: .05em;
  user-select: none;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.controls button, button {
  color: #4976a6;
  cursor: pointer;
  user-select: none;
  background-color: #333;
  border: none;
  border-radius: 6px;
  padding: .5rem 1.2rem;
  font-family: Segoe UI, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .3s, box-shadow .3s;
  box-shadow: 0 2px 6px #00000080;
}

button:hover, button:focus {
  background-color: #444;
  outline: none;
  box-shadow: 0 3px 8px #ffffff1a;
}

.board {
  flex-wrap: nowrap;
  justify-content: flex-start;
  gap: 1.5rem;
  width: 100%;
  padding-bottom: 1rem;
  display: flex;
  overflow-x: auto;
}

.board::-webkit-scrollbar-thumb {
  background-color: #090909;
}

.column {
  background-color: #a10e0e;
  border-radius: 10px;
  flex-direction: column;
  flex: 0 0 240px;
  min-width: 0;
  max-height: 80vh;
  padding: .75rem;
  display: flex;
  overflow-y: auto;
  box-shadow: 0 1px 6px #0006;
}

.column-title {
  color: #1c87f1;
  user-select: none;
  border-bottom: 2px solid #555;
  margin-bottom: .75rem;
  padding-bottom: .3rem;
  font-size: 1.1rem;
  font-weight: 700;
}

.task {
  cursor: grab;
  user-select: none;
  color: #0a286a;
  background-color: #3a3a3a;
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: .75rem;
  padding: .75rem 1rem;
  transition: background-color .15s;
  display: flex;
  box-shadow: 0 1px 4px #0a09094d;
}

.task:hover {
  background-color: #4a4a4a;
}

.task.done {
  color: #050505;
  box-shadow: none;
  background-color: #444;
  text-decoration: line-through;
}

.task button {
  color: #080808;
  cursor: pointer;
  user-select: none;
  background: none;
  border: none;
  padding: 0;
  font-size: 1.1rem;
}

.task button:hover {
  color: #74a5e6;
}

input[type="text"], select {
  color: #f0f0f0;
  box-sizing: border-box;
  background: #2b2b2b;
  border: 1.5px solid #555;
  border-radius: 6px;
  padding: .5rem .75rem;
  font-family: Segoe UI, sans-serif;
  font-size: 1rem;
  font-weight: 500;
}

input[type="text"]:focus, select:focus {
  border-color: #aaa;
  outline-color: #0000;
  box-shadow: 0 0 8px #aaa;
}

input[type="text"], select {
  min-width: 150px;
  margin-bottom: 6px;
  margin-right: 10px;
}

select.assign-dropdown {
  width: 100%;
  margin-top: 6px;
  margin-bottom: 6px;
}

input::placeholder {
  color: #999;
  opacity: .7;
  font-family: Segoe UI, sans-serif;
  font-style: italic;
  font-weight: 500;
}

label {
  font-family: Segoe UI, sans-serif;
  font-weight: 500;
  color: #fff !important;
}
